body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PTSans", Arial, sans-serif;
  font-size: 14px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  background-color: #f2f2f2;
}
.heading {
  font-size: 18px;
  text-align: center;
}
.page-header {
  display: flex;
  flex-direction: column;
}
.page-header__logo {
  position: absolute;
  top: 223px;
  left: 50%;
  z-index: 1;
  width: 102px;
  height: 83px;
  transform: translate(-50%, 0);
}
@media (min-width: 768px) {
  .page-header__logo {
    top: 0;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 188px;
    height: 96px;
  }
  .page-header__logo-image {
    width: 100%;
    height: 100%;
  }
}
.main-nav {
  display: flex;
  flex-direction: column;
  min-height: 279px;
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: 240px;
  right: 20px;
  padding: 0;
  width: 23px;
  height: 21px;
  border: none;
}
.main-nav__toggle--closed {
  top: 17px;
  height: 22px;
}
.main-nav__icon-menu {
  fill: #81b3d2;
  background-color: #ffffff;
}
.main-nav__icon-cross {
  fill: #ffffff;
  background-color: #81b3d2;
}
.main-nav__list {
  padding: 0;
  margin: 0;
}
@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    margin: 0;
    min-height: 56px;
  }
  .main-nav__toggle {
    display: none;
  }
}
