.main-content {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.main-content__header {
  position: relative;
  min-height: 267px;
  padding-top: 43px;
  padding-bottom: 29px;
  box-sizing: border-box;
}
.main-content__header::before {
  content: url("../img/bg-intro-triangle-mobile.svg");
  position: absolute;
  top: -26px;
  left: 0;
  width: 100%;
}
.main-content__header::after {
  content: url("../img/bg-triangle.svg");
  background-color: #81b3d2;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.main-content__form-header {
  position: relative;
  min-height: 203px;
  padding-top: 43px;
  padding-bottom: 20px;
  box-sizing: border-box;
  border-bottom: 1px solid #e5e5e5;
}
.main-content__form-header::before {
  content: url("../img/bg-intro-triangle-mobile.svg");
  position: absolute;
  top: -26px;
  left: 0;
  width: 100%;
}
.main-content__heading {
  margin: 0;
  margin-bottom: 35px;
}
.main-content__intro {
  margin: 0 20px;
}
@media (min-width: 768px) {
  .main-content__header {
    min-height: 206px;
    padding-top: 50px;
    padding-bottom: 55px;
    padding-right: 110px;
    padding-left: 110px;
  }
  .main-content__header::before {
    content: url("../img/bg-intro-triangle-tablet.svg");
    top: -57px;
  }
  .main-content__header::after {
    content: "";
  }
  .main-content__form-header {
    min-height: 206px;
    padding-top: 50px;
    padding-bottom: 55px;
    padding-right: 120px;
    padding-left: 120px;
    border-bottom: none;
  }
  .main-content__form-header::before {
    content: url("../img/bg-intro-triangle-tablet.svg");
    top: -57px;
  }
}
@media (min-width: 1200px) {
  .main-content__header {
    padding-right: 310px;
    padding-left: 310px;
  }
  .main-content__header::before {
    content: url("../img/bg-intro-triangle-desktop.svg");
  }
  .main-content__form-header {
    padding-right: 310px;
    padding-left: 310px;
  }
  .main-content__form-header::before {
    content: url("../img/bg-intro-triangle-desktop.svg");
  }
}
.advantage {
